<template>
  <div class="personal-nav fl">
    <div class="stu-infor">
      <div class="stu-avater">
        <img src="../../assets/default_avater.png" alt />
      </div>
      <div class="stu-name">{{username}}</div>
      <div class="sub-btn">
         <span style="color:gray"><!-- @click="mesNoti" -->
          <i class="iconfont icon-xinfeng"></i>
          消息通知
        </span>
        <span @click="setInfo">
          <i class="iconfont icon-shezhi1"></i>
          个人设置
        </span>
      </div>
    </div>
    <div class="stu-nav-content">
      <router-link
        class="stu-nav-bar"
        v-for="(item,index) in barList"
        :key="index"
        active-class="active"
        :to="item.path"
      >
        <i class="iconfont" :class="item.icon"></i>
        {{item.title}}
      </router-link>
    </div>
  </div>
</template>
<script>
/* import { PassThrough } from "stream"; */
import http from "../../utils/http";
// import api from "../../utils/api";
// import cookie from "@/utils/cookieOperate.js";
export default {
  props: {
    barList: Array
  },
  data() {
    return {
      canactive: true,
      userid: "",
      username: ""
      // imgSrc:
      //   "https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1541394127913&di=0557d44eb41c645b1691a060250da601&imgtype=0&src=http%3A%2F%2Fimg.pconline.com.cn%2Fimages%2Fupload%2Fupc%2Ftx%2Fwallpaper%2F1603%2F31%2Fc2%2F19819788_1459402903342_800x800.jpg"
    };
  },
  mounted() {
    if (http.logined()) {
      // 已登录
      let userInfo = http.getUser();
      if (userInfo) {
        this.userid = http.getUser().UserID;
        if (http.getUser().UserType == 1) this.GetThcMsg();
        else this.GetStuMsg();
      }
    }
  },
  methods: {
    async GetThcMsg() {
      let thcInfo = http.getThc();
      if (thcInfo) {
        this.username = http.getThc().TeacherName;
      }
    },
    async GetStuMsg() {
      let stuInfo = http.getStu();
      if (stuInfo) {
        this.username = http.getStu().StudentName;
      }
    },
    setInfo() {
      // console.log("点击跳转消息设置");
      this.$router.push("/main/setInfo/basicInfo");
      // console.log(this.$route.path, "现在路径是:");
    },
    mesNoti() {
      this.$router.push("/main/mesNotify/allMes");
      // console.log(this.$route.path, "现在路径是:");
    }
  }
};
</script>
<style lang="less" scoped>
.personal-nav {
  width: 220px;
  padding-bottom: 36px;
  background: #fff;
  text-align: left;
  user-select: none;
  margin-bottom: 25px;
  .stu-infor {
    width: 100%;
    padding: 25px 0;
    border-bottom: 1px solid #f1f5f9;
    .stu-avater {
      width: 82px;
      height: 82px;
      border-radius: 50%;
      overflow: hidden;
      margin: 8px auto;
      img {
        width: 100%;
        height: 100%;
      }
    }
    .stu-name {
      text-align: center;
      font-weight: 500;
      font-size: 1.1rem;
      margin-bottom: 12px;
    }
    .sub-btn {
      width: 100%;
      text-align: center;
      span {
        width: 48%;
        display: inline-block;
        font-size: 0.9rem;
        cursor: pointer;
        .iconfont {
          color: #3fa7fc;
          font-size: 1.2rem;
          vertical-align: middle;
        }
      }
      span:first-child {
        border-right: 1px solid #f1f5f9;
      }
    }
  }
  .stu-nav-content {
    padding-top: 18px;
    text-align: center;
    .stu-nav-bar {
      height: 32px;
      display: block;
      line-height: 32px;
      margin-top: 18px;
      color: #2c3e50;
      text-decoration: none;
      text-align: left;
      padding-left: 40px;
      .iconfont {
        color: #63a9ff;
        font-size: 1.1rem;
        margin-right: 25px;
      }
      &:hover {
        cursor: pointer;
      }
    }
    .active {
      color: #63a9ff;
    }
  }
}
</style>



